<template src="../../assets/html/main/main-program.html"></template>

<script>

export default {
  name: 'MainProgram',
  data () {
    return {
      isLogin: '',
      programSwiperOption: {
        slidesPerView: 1,
        pagination: {
          el: '.swiper-pagination'
        }
      },
      expSwiperOption: {
        slidesPerView: 1,
        spaceBetween: 10
      }
    }
  },
  methods: {
    goStoreInfo () {
      this.$router.push('/shop/detail?store_id=' + process.env.FLAGSHIP_STORE_ID)
    }
  }
}
</script>

<style lang="scss">
  @import 'src/assets/css/main';
</style>



