<template src="../../../assets/html/mypage/booking/booking-inquiry.html"></template>

<script>
import Login from '../../../pages/sso/page-login'

export default {
  name: 'MyBookingInquiry',
  data () {
    return {
      isTabActive: [true, false]
    }
  },
  methods: {
    clickTab (idx) {
      this.isTabActive = [false, false]
      this.isTabActive[idx] = true
    },
    async clickButton (type) {
      if (type === 'login') {
        this.$modal.show(Login, {
          // component props
        }, {
          // props
          type: 'full'
        }, {
          // event
        })
      } else {
      // test
        await this.alertBookingListNone() //            .

        await this.alertRequiredApplicant() //          .
        this.$refs.applicant.focus()

        await this.alertRequiredBookingNumber() //      .
        this.clickTab(0)
        this.$nextTick(() => {
          this.$refs.bookingNumber.focus()
        })

        await this.alertRequiredPhoneNumber() //       .
        this.clickTab(1)
        this.$nextTick(() => {
          this.$refs.phoneNumber.focus()
        })

        this.$nextTick(() => {
          this.$router.push('/my/booking/list')
        })
      }
    },
    alertRequiredApplicant () {
      return new Promise(resolve => {
        //   .
        this.$modal.show('dialog', {
          title: this.$t('my.alert-required-applicant-name'),
          buttons: [{
            title: this.$t('comm.confirm'),
            handler: () => {
              resolve()
              this.$modal.hide('dialog')
            }
          }]
        })
      })
    },
    alertRequiredBookingNumber () {
      return new Promise(resolve => {
        //  .
        this.$modal.show('dialog', {
          title: this.$t('my.alert-required-booking-number'),
          buttons: [{
            title: this.$t('comm.confirm'),
            handler: () => {
              resolve()
              this.$modal.hide('dialog')
            }
          }]
        })
      })
    },
    alertRequiredPhoneNumber () {
      return new Promise(resolve => {
        //  .
        this.$modal.show('dialog', {
          title: this.$t('my.alert-required-phone-number'),
          buttons: [{
            title: this.$t('comm.confirm'),
            handler: () => {
              resolve()
              this.$modal.hide('dialog')
            }
          }]
        })
      })
    },
    alertBookingListNone () {
      return new Promise(resolve => {
        //   .
        this.$modal.show('dialog', {
          title: this.$t('my.booking-list-none'),
          buttons: [{
            title: this.$t('comm.confirm'),
            handler: () => {
              resolve()
              this.$modal.hide('dialog')
            }
          }]
        })
      })
    }
  }
}
</script>

<style lang="scss">
  @import 'src/assets/css/mypage';
</style>



