<template src="../../assets/html/etc/combine-certify.html"></template>
<script>
import ModalCombineInfo from './ModalCombineInfo'
import * as USER from '../../js/user.js'

export default {
  name: 'CombineCertify',
  data () {
    return {
      certCode: ''
    }
  },
  methods: {
    certify () {
      if (this.certCode.length === 0) {
        this.$modal.show('dialog', {
          title: this.$t('mytag.please-certification'),
          buttons: [{
            title: this.$t('comm.confirm'),
            handler: () => {
              this.$modal.hide('dialog')
            }
          }]
        })
        return
      }

      if (this.certCode.length > 0 && this.certCode.length !== 4) {
        this.$modal.show('dialog', {
          title: this.$t('mytag.please-wrong-number'),
          buttons: [{
            title: this.$t('comm.confirm'),
            handler: () => {
              this.$modal.hide('dialog')
            }
          }]
        })
      }
      USER.signInMDN(this.$route.query.MDN, this.$route.query.CERT_KEY, this.certCode).then(result => {
        if (result.RET_CODE === 0) {
          this.combineInfo()
        }
      })
    },
    combineInfo () {
      // TODO:    migrate API     
      this.$modal.show(ModalCombineInfo, {}, {}, {
        'complete-combine': () => {
          this.$modal.show('dialog', {
            title: this.$t('etc.complete-combine-member'),
            buttons: [{
              title: this.$t('comm.confirm'),
              handler: () => {
                this.$modal.hide('dialog')
                this.$router.push('/setting')
              }
            }]
          })
        }
      })
    }
  }
}
</script>

<style lang="scss">
  @import '../../assets/css/etc';
</style>



