<template src="../../assets/html/etc/combine.html"></template>

<script>
import * as USER from '../../js/user.js'

export default {
  name: 'Combine',
  data () {
    return {
      number: '010',
      checkTermFlag: false
    }
  },
  methods: {
    sendCertifyNumber () {
      if (!this.checkTermFlag) {
        this.$modal.show('dialog', {
          title: this.$t('sev.alert-certification-agree'),
          buttons: [{
            title: this.$t('comm.confirm'),
            handler: () => {
              this.$modal.hide('dialog')
            }
          }]
        })
        return
      }

      if (this.number.length !== 11) {
        this.$modal.show('dialog', {
          title: this.$t('etc.please-input-temp-number'),
          buttons: [{
            title: this.$t('comm.confirm'),
            handler: () => {
              this.$modal.hide('dialog')
            }
          }]
        })
        return
      }

      USER.requestSMS(this.number).then(result => {
        if (result.RET_CODE === 0) {
          this.$router.push('/combine/certify?CERT_KEY=' + result.CERT_KEY + '&MDN=' + this.number)
        }
      })
    }
  }
}
</script>

<style lang="scss">
@import '../../assets/css/etc';
</style>



