  /*.modal-entrance .modal__body .text__title::before{background-image:url(../image/entrance@2x.png)}*/
  /*.service__trip .modal-entrance .modal__body .text__title::before{background-image:url(../image/entrance-skt@2x.png)}*/
  /*.modal-coin .modal__body .text__title::before{background-image:url(../image/coin@2x.png)}*/
  /*.service__trip .modal-coin .modal__body .text__title::before{background-image:url(../image/coin-skt@2x.png)}*/

  .pagelist-title {
    margin: 20px 0 0;
    padding: 5px;
    color: #fff;
    font-size: 14px
  }

  .pagelist-title:first-of-type {
    margin-top: 10px;
  }

  .app__SKT .pagelist-title {
    background-color: #e98e42
