  .pagelist-item {
    display: flex;
    border-bottom: 1px solid #eee
  }

  .pagelist-item .link {
    display: block;
    padding: 10px;
    color: #000;
    font-size: 13px;
    font-weight: 500
  }

  .pagelist-item .link[href^="#"] {
    color: #eee
  }

  .pagelist-item .date {
    display: block;
    width: 110px;
    margin-left: auto;
    padding: 12px 0 10px;
    font-size: 0;
    color: #666;
  }

  .pagelist-item .date span {
    display: inline-block;
    position: relative;
    width: 50%;
    font-size: 12px;
    text-align: center;
  }

  .pagelist-item .date span:nth-of-type(2):after {
    content: '/';
    position: absolute;
    top: 0;
    left: 0;
    font-size: 12px;
  }
</style>



