<template>
  <div>
    <header class="header">
      <h1 class="logo"></h1></header>
  <div class="contents" v-if="verify === false">
    <p class="tit"> </p>
    <p class="nottit">   <br>     !</p>
    <br>
    <img src="../assets/images/dummy/faceid.jpeg">
    <button @click="callNativeScheme" class="btn-wrap"> </button>
  </div>
    <div class="contents" v-if="verify === true">
      <p class="tit">{{name}}  </p>
      <p class="nottit">
        . :)</p>
      <button @click="logOutButton" class="btn-wrap"></button>
    </div>
    <div class="contents" v-if="verify === 'ERROR'">
      <p class="tit"> </p>
      <p class="nottit">   .</p>
    <button @click="logOutButton" class="btn-wrap"></button>
    </div>
  </div>
</template>

<script>
import * as NATIVE from '../js/native.js'
import * as USER from '../js/user.js'

export default {
  name: 'page-verify',
  data () {
    return {
      verify: false,
      name: ''
    }
  },
  methods: {
    verifyComplete (response) {
      USER.getMyInfo(response.ACCESS_TOKEN).then(result => {
        if (result.RET_CODE !== 0) {
          this.verify = 'ERROR'
          return
        }
        this.name = result.NAME
        this.verify = true
      })
    },
    callNativeScheme () {
      NATIVE.faceRecognize('A')
    },
    logOutButton () {
      this.verify = false
    }
  }
}
</script>

<style scoped>
.header {
  text-align: center;
}
.logo {
  display: inline-block;
  width:112px;
  height:32px;
  margin: 12px 0;
  background-image: url(/static/img/ico_logo.93052b0.svg);
  background-size: 200px auto;
  background-repeat: no-repeat;
  background-position: 0 -120px;
}
.tit {
  margin: 48px 0 20px;
  color: #000;
  font-size: 28px;
  line-height: 42px;
  letter-spacing: -.22px;
}
.nottit {
  font-size: 15px;
  line-height: 22px;
  letter-spacing: -.08px;
  color: #505050;
}
.contents {
  position: fixed;
  width: 100%;
  height: 100%;
  padding: 56px 32px 32px;
}
.btn-wrap {
  font-size: 18px;
  position: absolute;
  left: 32px;
  bottom: 32px;
  width: calc(100% - 64px);
  text-align: center;
  line-height: 65px;
  display: inline-block;
  color: #505050;
  background-color: #00b0eb59; /* Green */
}
</style>



