<template src="../../assets/html/service/counselor.html"></template>

<script>
export default {
  name: 'Counselor',
  data () {
    return {
      status: 0, // 0: , 1: , 2:  
      count: 59,
      countText: '59',
      timer: {}
    }
  },
  watch: {
    status (status) {
      if (status === 2) {
        this.reset()

        // TODO -  
      }
    }
  },
  methods: {
    countDown () {
      this.count--
      this.countText = this.count
      if (this.count < 10) {
        this.countText = '0' + this.countText
      }
      if (this.count < 0) {
        this.reset()
        this.status = 1
      }
    },
    reset () {
      this.$modal.hide('dialog')
      this.timer.stop()
      this.count = 59
      this.countText = '59'
    },
    clickCancel () {
      this.$modal.show('dialog', {
        title: '   ?',
        buttons: [
          {
            title: '',
            handler: () => {
              this.reset()
              this.status = 1

              // TODO -   
            }
          },
          {
            title: ''
          }
        ]
      })
    },
    clickRetry () {
      this.reset()
      this.status = 0
      this.timer.start()
    }
  },
  created () {
    let _vue = this

    this.timer = {
      interval: null,
      start () {
        if (!this.interval) {
          this.interval = setInterval(() => {
            _vue.countDown()
          }, 1000)
        }
      },
      stop () {
        if (this.interval) {
          clearInterval(this.interval)
          this.interval = null
        }
      }
    }
  },
  mounted () {
    this.timer.start()
  }
}
</script>

<style lang="scss">
  @import 'src/assets/css/service';
</style>



