<template src="../../assets/html/etc/join-certify.html"></template>
<script>
import ModalCombineInfo from './ModalCombineInfo'

export default {
  name: 'JoinCertify',
  data () {
    return {
      certCode: ''
    }
  },
  mounted () {
  },
  methods: {
    certify () {
      //   .
      this.$modal.show('dialog', {
        title: this.$t('mytag.please-certification'),
        buttons: [{
          title: this.$t('comm.confirm'),
          handler: () => {
            this.$modal.hide('dialog')

            //   .<br> .
            this.$modal.show('dialog', {
              title: this.$t('mytag.please-wrong-number'),
              buttons: [{
                title: this.$t('comm.confirm'),
                handler: () => {
                  this.$modal.hide('dialog')

                  //    .
                  this.combineInfo()
                }
              }]
            })
          }
        }]
      })
    },
    combineInfo () {
      this.$modal.show(ModalCombineInfo, {
        // component props
      }, {
        // modal props,
      }, {
        // event
        'complete-combine': e => {
          //  .
          this.$modal.show('dialog', {
            title: this.$t('etc.complete-regular-member'),
            buttons: [{
              title: this.$t('comm.confirm'),
              handler: () => {
                this.$modal.hide('dialog')

                //  
                this.$router.push('/')
              }
            }]
          })
        }
      })
    }
  }
}
</script>

<style lang="scss">
  @import '../../assets/css/etc';
</style>



