import Vue from 'vue'
import Router from 'vue-router'

import pagelist from '../pages/_pagelist'
import guide from '../pages/_guide'

// 
import AllMenuConfirm from '../pages/_confirmation/page-all-menu'
import AllMenuLoginConfirm from '../pages/_confirmation/page-all-menu-login'
import WishNoneConfirm from '../pages/_confirmation/page-wish-none'
import BookingNone from '../pages/_confirmation/page-booking-none'
import BookingInfoLogout from '../pages/_confirmation/page-booking-info-logout'
import FaqNone from '../pages/_confirmation/page-faq-none'
import CounselorComplete from '../pages/_confirmation/page-counselor-complete'
import MainIsLogin from '../pages/_confirmation/page-main-login'
import PayNone from '../pages/_confirmation/page-pay-none'
import BookingListNone from '../pages/_confirmation/page-booking-list-none'
import SettingLogout from '../pages/_confirmation/page-setting-logout'
import ProductButtonCase from '../pages/_confirmation/page-product-button-case'

// comm
import Error400 from '../pages/error/page-error400'
import Error500 from '../pages/error/page-error500'
import SystemCheck from '../pages/error/page-system-check'

// main
import Main from '../pages/page-main'
import MainLogin from '../pages/main/page-main-login'
import MainAppDown from '../pages/main/page-main-appdown'

// mytag
import MyTagLogin from '../pages/mytag/page-tag-login'
import MyTagCertify from '../pages/mytag/page-tag-certify'
import MyTagReissue from '../pages/mytag/page-tag-reissue'
import MyTagServey from '../pages/mytag/page-tag-survey'
import MyTagMms from '../pages/mytag/page-tag-mms'
import MyTagAdditional from '../pages/mytag/page-tag-additional'

// prd
import Product from '../pages/product/page-product'
import Experience from '../pages/product/page-experience'

// pay
import PayLogin from '../components/pay/ModalPayLogin'
import Pay from '../pages/pay/page-pay'
import PayComplete from '../pages/pay/page-pay-complete'
import PayFail from '../pages/pay/page-pay-fail'

// sev
import Booking from '../pages/service/page-booking'
import BookingDate from '../pages/service/page-booking-date'
import BookingInfo from '../pages/service/page-booking-info'
import BookingSurvey from '../pages/service/page-booking-survey'
import BookingSurveyComplete from '../pages/service/page-booking-survey-complete'
import BookingComplete from '../pages/service/page-booking-complete'
import ReadMap from '../components/service/booking/ModalReadMap'
import Counselor from '../pages/service/page-counselor'
import Faq from '../pages/service/page-faq'
import Consult from '../pages/service/page-consult'
import ConsultComplete from '../pages/service/page-consult-complete'

// my
import Wish from '../pages/mypage/page-wish'
import MyPayList from '../pages/mypage/page-pay-list'
import PayReceipt from '../components/mypage/ModalPayReceipt'
import MyBookingInquiry from '../pages/mypage/page-booking-inquiry'
import MyBookingList from '../pages/mypage/page-booking-list'
import MyBookingDetail from '../pages/mypage/page-booking-detail'
import MyBookingModify from '../pages/mypage/page-booking-modify'
import MyBookingSurvey from '../pages/mypage/page-booking-survey'
import MyBookingComplete from '../pages/mypage/page-booking-complete'
import Checkin from '../pages/mypage/page-checkin'
import CheckinModify from '../pages/mypage/page-checkin-modify'
import CheckinRegister from '../pages/mypage/page-checkin-register'
import MyCouponList from '../pages/mypage/page-coupon-list'

// sso
import Login from '../pages/sso/page-login'
import Kiosk from '../pages/sso/page-kiosk'
import Success from '../pages/sso/page-success'
import Certify from '../pages/sso/page-certify'

// shop
import ShopList from '../pages/shop/page-shop'
import ShopDetail from '../pages/shop/page-shop-detail'

// etc
import FamilySite from '../pages/etc/page-family-site'
import CorpInfo from '../pages/etc/page-corp-info'
import Setting from '../pages/etc/page-setting'
import Member from '../pages/etc/page-member'
import Terms from '../pages/etc/page-terms'
import Leave from '../pages/etc/page-leave'
import LeaveComplete from '../pages/etc/page-leave-complete'
import PolicyAdvg from '../pages/etc/page-policy-advg'
import PolicyBio from '../pages/etc/page-policy-bio'
import PolicyPrivacy from '../pages/etc/page-policy-privacy'
import PolicyPrivacyOpt from '../pages/etc/page-policy-privacy-opt'
import PolicyService from '../pages/etc/page-policy-service'
import PolicyConflict from '../pages/etc/page-conflict-process'
import Combine from '../pages/etc/page-combine'
import CombineCertify from '../pages/etc/page-combine-certify'
import Join from '../pages/etc/page-join'
import JoinCertify from '../pages/etc/page-join-certify'

// event
import EventList from '../pages/event/page-event-list'
import EventView from '../pages/event/page-event-view'

// test
import TestRoom21 from '../pages/page-21'
import TestFaceVerify from '../pages/page-test-verify'

import Redirect from '../pages/page-redirect'

Vue.use(Router)

export default new Router({
  mode: 'history',
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  },
  routes: [
    {path: '/', redirect: {name: 'Main'}},

    {path: '/pagelist', name: 'pagelist', component: pagelist},
    {path: '/guide', name: 'guide', component: guide},

    // 
    {path: '/confirm/allmenu', name: 'AllMenuConfirm', component: AllMenuConfirm}, // 
    {path: '/confirm/allmenulogin', name: 'AllMenuLoginConfirm', component: AllMenuLoginConfirm}, //  - 
    {path: '/confirm/wishnone', name: 'WishNoneConfirm', component: WishNoneConfirm}, //  none
    {path: '/confirm/:type/none', name: 'BookingNone', component: BookingNone}, // , ,  
    {path: '/confirm/readmap', name: 'ReadMap', component: ReadMap}, // 
    {path: '/confirm/faqnone', name: 'FaqNone', component: FaqNone}, //  none
    {path: '/confirm/infologout', name: 'BookingInfoLogout', component: BookingInfoLogout}, //  none
    {path: '/confirm/counselorcomplete', name: 'CounselorComplete', component: CounselorComplete}, //  
    {path: '/confirm/paylogin', name: 'PayLogin', component: PayLogin}, //  
    {path: '/confirm/main', name: 'MainIsLogin', component: MainIsLogin}, //  
    {path: '/confirm/receipt', name: 'PayReceipt', component: PayReceipt}, // 
    {path: '/confirm/paylistnone', name: 'PayNone', component: PayNone}, //  none
    {path: '/confirm/bookinglistnone', name: 'BookingListNone', component: BookingListNone}, //    none
    {path: '/confirm/settinglogout', name: 'SettingLogout', component: SettingLogout}, // 
    {path: '/confirm/productbuttoncase', name: 'ProductButtonCase', component: ProductButtonCase}, // 
    {path: '/confirm/mytag/servey', name: 'MyTagServey', component: MyTagServey}, // My Tag 

    // comm
    {path: '/error/400', name: 'Error400', component: Error400}, // 400 
    {path: '/error/500', name: 'Error500', component: Error500}, // 500 
    {path: '/error/check', name: 'SystemCheck', component: SystemCheck}, // 

    // main
    {path: '/main/:menu', name: 'Main', component: Main}, // , //store, program, qr, mytag
    {path: '/mainlogin', name: 'MainLogin', component: MainLogin}, //  
    {path: '/mainappdown', name: 'MainAppDown', component: MainAppDown}, //  

    // my tag
    {path: '/mytag/login', name: 'MyTagLogin', component: MyTagLogin}, // my tag 
    {path: '/mytag/certify/:mdn', name: 'MyTagCertify', component: MyTagCertify}, // my tag 
    {path: '/mytag/reissue', name: 'MyTagReissue', component: MyTagReissue}, // my tag 
    {path: '/mytag/mms/:mdn', name: 'MyTagMms', component: MyTagMms}, // mms 
    {path: '/mytag/additional', name: 'MyTagAdditional', component: MyTagAdditional}, // 

    // prd
    {path: '/prd/:productId?', name: 'Product', component: Product}, // 
    {path: '/experience', name: 'Experience', component: Experience}, //  - 

    // pay
    {path: '/pay', name: 'Pay', component: Pay}, //  
    {path: '/pay/complete', name: 'PayComplete', component: PayComplete}, //  
    {path: '/pay/complete/vm', name: 'PayCompleteVm', component: PayComplete}, //   ()
    {path: '/pay/fail', name: 'PayFail', component: PayFail}, //  

    // sev
    {path: '/sev/booking/:type', name: 'Booking', component: Booking}, // , ,   - type: program, experience, counseling
    {path: '/sev/booking/:type/date/:selectType', name: 'BookingDate', component: BookingDate}, //  
    {path: '/sev/booking/:type/complete', name: 'BookingComplete', component: BookingComplete}, //  
    {path: '/sev/booking/:type/info', name: 'BookingInfo', component: BookingInfo}, //  
    {path: '/sev/booking/:type/survey', name: 'Survey', component: BookingSurvey}, //  
    {path: '/sev/booking/:type/surveycomplete', name: 'SurveyComplete', component: BookingSurveyComplete}, //  
    {path: '/sev/counselor', name: 'Counselor', component: Counselor}, //  
    {path: '/sev/faq', name: 'faq', component: Faq}, //  
    {path: '/sev/consult', name: 'Consult', component: Consult}, //  
    {path: '/sev/consult/complete', name: 'ConsultComplete', component: ConsultComplete}, //   

    // my
    {path: '/my/wish', name: 'Wish', component: Wish}, // 
    {path: '/my/paylist', name: 'MyPayList', component: MyPayList}, // 
    {path: '/my/booking/inquiry', name: 'MyBookingInquiry', component: MyBookingInquiry}, //   
    {path: '/my/booking/list', name: 'MyBookingList', component: MyBookingList}, //  
    {path: '/my/booking/:type/detail/:status', name: 'MyBookingDetail', component: MyBookingDetail}, //  
    {path: '/my/booking/:type/modify', name: 'MyBookingModify', component: MyBookingModify}, //  
    {path: '/my/booking/:type/survey', name: 'MyBookingSurvey', component: MyBookingSurvey}, //  
    {path: '/my/booking/:type/complete', name: 'MyBookingComplete', component: MyBookingComplete}, //  
    {path: '/my/checkin', name: 'Checkin', component: Checkin}, //   
    {path: '/my/modify', name: 'CheckinModify', component: CheckinModify}, //   
    {path: '/my/register', name: 'CheckinRegister', component: CheckinRegister}, //    
    {path: '/my/coupon', name: 'MyCouponList', component: MyCouponList}, //  

    // sso
    {path: '/sso/login', name: 'Login', component: Login}, // 
    {path: '/sso/kiosk', name: 'Kiosk', component: Kiosk}, // kiosk 
    {path: '/sso/success', name: 'Success', component: Success}, // kiosk  
    {path: '/sso/certify', name: 'Certify', component: Certify}, //  

    // shop
    {path: '/shop/list', name: 'Shop', component: ShopList}, // 
    {path: '/shop/detail', name: 'ShopDetail', component: ShopDetail}, //  

    // etc
    {path: '/familysite', name: 'FamilySite', component: FamilySite}, //  
    {path: '/corpinfo', name: 'CorpInfo', component: CorpInfo}, //  
    {path: '/setting', name: 'Setting', component: Setting}, // 
    {path: '/member', name: 'Member', component: Member}, // 
    {path: '/terms', name: 'Terms', component: Terms}, //  
    {path: '/leave', name: 'Leave', component: Leave}, //  
    {path: '/leave/complete', name: 'LeaveComplete', component: LeaveComplete}, //   
    {path: '/policy/advg', name: 'PolicyAdvg', component: PolicyAdvg}, //    
    {path: '/policy/bio', name: 'PolicyBio', component: PolicyBio}, // ()  
    {path: '/policy/privacy', name: 'PolicyPrivacy', component: PolicyPrivacy}, //     ()
    {path: '/policy/privacyopt', name: 'PolicyPrivacyOpt', component: PolicyPrivacyOpt}, //    ()
    {path: '/policy/service', name: 'PolicyService', component: PolicyService}, //  _ 
    {path: '/policy/conflict', name: 'PolicyConflict', component: PolicyConflict}, //  _ 
    {path: '/combine', name: 'Combine', component: Combine}, //   
    {path: '/combine/certify', name: 'CombineCertify', component: CombineCertify}, //    -  
    {path: '/join', name: 'Join', component: Join}, //  
    {path: '/join/certify', name: 'JoinCertify', component: JoinCertify}, //   -  

    // event
    {path: '/event/list', name: 'EventList', component: EventList}, // 
    {path: '/event/view/:type', name: 'EventView', component: EventView}, // 

    // test
    {path: '/test/room21', name: 'TestRoom21', component: TestRoom21},
    {path: '/test/verify', name: 'TestFaceVerify', component: TestFaceVerify},

    {path: '/redirect', name: 'Redirect', component: Redirect}

  ]
})



