import axios from 'axios'
import * as COMMON from './common.js'

const MLS_URL = process.env.SERVER_URL + '/mls'

const sortJsonArray = (a, b) => {
  if (a.share === b.share) { return 0 } return a.share < b.share ? 1 : -1
}

export const getAIPickDevice = (userId) => {
  let request = {
    'reqId': 'test_reqId',
    'userId': userId,
    'channelIds': ['' +
    '' +
    ''],
    'saleOrgId': '0000000109'
  }

  return axios.post(MLS_URL + '/getAIPickInfo', request).then(response => {
    if (response.status === 200) {
      // console.log(JSON.parse(response.data.respBody).results.tshop_device)
      return JSON.parse(response.data.respBody).results.tshop_device[0].id
    } else {
      // TODO: Error Alert
    }
  })
}

export const getAIPickVAS = (userId) => {
  let request = {
    'reqId': 'test_reqId',
    'userId': userId,
    'channelIds': ['tshop_vas_top3'],
    'saleOrgId': 'A000700000'
  }

  return axios.post(MLS_URL + '/getAIPickInfo', request, COMMON.getHeader()).then(response => {
    if (response.status === 200) {
      return JSON.parse(response.data.respBody).results.tshop_vas_top3
    } else {
      // TODO: Error Alert
    }
  })
}

export const getStatistics = (deviceGroupId) => {
  let request = {
    'reqId': 'test_reqId',
    'userId': deviceGroupId,
    'channelIds': ['dist_color_nm', 'dist_eqp_mdl_size']
  }

  return axios.post(MLS_URL + '/getProfileStat', request).then(response => {
    if (response.status === 200) {
      // console.log(JSON.parse(response.data.respBody).results.dist_eqp_mdl_size)
      let sortColor = JSON.parse(response.data.respBody).results.dist_color_nm.sort(sortJsonArray)
      let sortSize = JSON.parse(response.data.respBody).results.dist_eqp_mdl_size.sort(sortJsonArray)

      let result = {
        'dist_color_nm': sortColor,
        'dist_eqp_mdl_size': sortSize
      }
      return result
    } else {
      // TODO: Error Alert
    }
  })
}

export const getStatisticsByAgeAndSex = (deviceGroupId, age, sex) => {
  if (sex === '') {
    sex = ' '
  } else if (sex === '') {
    sex = ' '
  }

  let request = {
    'reqId': 'test_reqId',
    'userId': deviceGroupId,
    'channelIds': ['dist_color_nm_by_age_sex', 'dist_eqp_mdl_size_by_age_sex']
  }

  return axios.post(MLS_URL + '/getProfileStat', request).then(response => {
    if (response.status === 200) {
      let results = JSON.parse(response.data.respBody).results
      let colorByAgeSex = []
      let sizeByAgeSex = []

      for (let i in results.dist_color_nm_by_age_sex) {
        if (results.dist_color_nm_by_age_sex[i].age_band === 'AGE_' + age.toString() &&
          results.dist_color_nm_by_age_sex[i].sex_cd_nm === sex) {
          colorByAgeSex.push(results.dist_color_nm_by_age_sex[i])
        }
      }

      for (let i in results.dist_eqp_mdl_size_by_age_sex) {
        if (results.dist_eqp_mdl_size_by_age_sex[i].age_band === 'AGE_' + age.toString() &&
          results.dist_eqp_mdl_size_by_age_sex[i].sex_cd_nm === sex) {
          sizeByAgeSex.push(results.dist_eqp_mdl_size_by_age_sex[i])
        }
      }

      let sortColor = colorByAgeSex.sort(sortJsonArray)
      let sortSize = sizeByAgeSex.sort(sortJsonArray)

      let result = {
        'dist_color_nm': sortColor,
        'dist_eqp_mdl_size': sortSize
      }
      return result
    } else {
      // TODO: Error Alert
    }
  })
}

export const getOrgProductSale = (orgId) => {
  let request = {
    reqId: 'test_reqId',
    profileId: 'org',
    itemId: orgId,
    keys: ['product_sales']
  }

  return axios.post(MLS_URL + '/getItemProfile', request).then(response => {
    if (response.status === 200) {
      return JSON.parse(response.data.respBody).results.product_sales
    } else {
      // TODO: Error Alert
    }
  })
}



