<template src="../../assets/html/etc/pop-combine-info.html"></template>

<script>
// import * as USER from '../../js/user.js'

export default {
  name: 'ModalInfo',
  methods: {
    cancel () {
      this.$emit('close')
    },
    combine () {
      // migrate
      // USER.migrateToTID()
      this.$emit('close')
      this.$parent.$emit('complete-combine')
    }
  }
}
</script>



