<template>
  <div style="font-size:20px" class="wrap">
    <br>
      Galaxy S20+ 5G <br>
      <toggle-button @change="deviceToggle($event.value, '000004234')"
                     color="#82C7EB"
                     :width="100"
                     :height="50"
      /><br>
      Galaxy S20 5G<br>
    <toggle-button @change="deviceToggle($event.value, '000004233')"
                   color="#82C7EB"
                   :width="100"
                   :height="50"
    /><br>
      Galaxy Note10 5G <br>
    <toggle-button @change="deviceToggle($event.value, '000003772')"
                   color="#82C7EB"
                   :width="100"
                   :height="50"
    /><br>
      IPHONE_11 <br>
    <toggle-button @change="deviceToggle($event.value, '000003972')"
                   color="#82C7EB"
                   :width="100"
                   :height="50"
    /><br>
      IPHONE_11_PRO <br>
    <toggle-button @change="deviceToggle($event.value, '000003973')"
                   color="#82C7EB"
                   :width="100"
                   :height="50"
    /><br>
      IPHONE_11_PRO_MAX <br>
    <toggle-button @change="deviceToggle($event.value, '000003974')"
                   color="#82C7EB"
                   :width="100"
                   :height="50"
    /><br>
      V50 <br>
    <toggle-button @change="deviceToggle($event.value, '000003572')"
                   color="#82C7EB"
                   :width="100"
                   :height="50"
    /><br>
      V50S <br>
    <toggle-button @change="deviceToggle($event.value, '000003952')"
                   color="#82C7EB"
                   :width="100"
                   :height="50"
    /><br>
     </div>
</template>

<script>
import axios from 'axios'
import { ToggleButton } from 'vue-js-toggle-button'
import Vue from 'vue'

Vue.component('ToggleButton', ToggleButton)

export default {
  name: 'page-room21',
  component: {
    ToggleButton
  },
  methods: {
    deviceToggle (value, groupId) {
      if (value === true) {
        this.deviceOn(groupId)
      } else if (value === false) {
        this.deviceOff(groupId)
      }
    },
    deviceOn (groupId) {
      let request = {
        'action': 'ON',
        'productGrpId': groupId,
        'stCode': 'TPS012'
      }

      axios.post('https://dev.sktplace.net/api/kiosk/front/api/ADT', request).then(response => {
        if (response.status !== 200) {
          alert('ERROR')
        }
      })
    },
    deviceOff (groupId) {
      let request = {
        'action': 'OFF',
        'productGrpId': groupId,
        'stCode': 'TPS012'
      }

      axios.post('https://dev.sktplace.net/api/kiosk/front/api/ADT', request).then(response => {
        if (response.status !== 200) {
          alert('ERROR')
        }
      })
    }
  }
}
</script>



